@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;800&display=swap');
:root {
  --bordercolour: #0c8aff;
  --editcolour: #14ff9d;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Ubuntu400', sans-serif;
}
.rainbowtoggle {
  padding: 14px;
  margin-top: -150px;
  /* border: none; */
  /* border-radius: 0 4px 4px 0; */
  cursor: pointer;
  outline: none;
  font-family: 'Ubuntu';
  
  /* background: linear-gradient(
    90deg,
    rgba(93, 12, 255, 1) 0%,
    rgba(155, 0, 250, 1) 100%
  ); */
  border: 3px solid var(--bordercolour);
  
  border-radius: 10px;
  background: none;
  color: #fff;
  text-transform: capitalize;
}
body {
  background: #161a2b;
}
.todo-app {
  font-family: 'Ubuntu';
  display: flex;
  flex-direction: column;
  justify-content: start;
  width: 90%;
  background: transparent;
  text-align: center;
  margin: 128px auto;
  
  border: 3px solid var(--bordercolour);
  
  border-radius: 10px;
  padding-bottom: 32px;
  box-shadow: #000 0px 0px 20px 0px;
  text-shadow: #000 0px 0px 20px;
  animation: rainbow-anim 15s infinite; 
}
@media (min-width: 720px) {
  .todo-app {
    width: 40%
  }
}

div {
  font-family: 'Ubuntu';
}
h1 {
  margin: 32px 0;
  color: #fff;
  font-size: 24px;
}



.todo-form {
  margin-bottom: 32px;
}

.todo-input {
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  /* border: 2px solid #5d0cff; */
  background: transparent;
  
  border: 3px solid var(--bordercolour);
  
  border-radius: 10px;
  outline: none;
  font-family: 'Ubuntu';
  width: 65%;
  background: transparent;
  color: #fff;
  box-shadow: #000 0px 0px 15px;
}

.button {
  cursor: pointer;
  padding: 40px;
}
.todo-input::placeholder {
  color: #e2e2e2;
}

.todo-button {
  padding: 14px;
  margin-left: 5px;
  /* border: none; */
  /* border-radius: 0 4px 4px 0; */
  cursor: pointer;
  outline: none;
  font-family: 'Ubuntu';
  
  /* background: linear-gradient(
    90deg,
    rgba(93, 12, 255, 1) 0%,
    rgba(155, 0, 250, 1) 100%
  ); */
  border: 3px solid var(--bordercolour);
  
  border-radius: 10px;
  background: none;
  color: #fff;
  text-transform: capitalize;
}
.todo-buttons:hover {
  transition: all .2s ease-in-out;
  transform: scale(1.02);
}
.todo-button:hover {
  background: var(--bordercolour);
}
.todo-input.edit {
  border: 3px solid var(--editcolour);
  
  border-radius: 10px;
}

.todo-button.edit {
  background: none;
  border: 3px solid var(--editcolour);
  
  border-radius: 10px;
  padding: 14px 21px;
}
.todo-button.edit:hover {
  background: var(--editcolour)
}

.todo-container {
  display: flex;
  flex-direction: row;
  position: relative;
}

.todo-row {
  user-select: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px auto;
  color: #fff;
  text-shadow: #000 0px 0px 15px;
  /* background: linear-gradient(
    90deg,
    rgb(36, 13, 241) 0%,
    rgb(57, 77, 255) 100%
  ); */
  background: transparent;
  border: 3px solid var(--bordercolour);
  border-radius: 10px;

  padding: 13px;
  /* border-radius: 5px; */
  width: 90%;
  box-shadow: #000 0px 0px 15px;
}
.todo-row:hover {
  transition: all .2s ease-in-out;
  transform: scale(1.02);
  background: var(--bordercolour);
}



.icons {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}

.delete-icon {
  margin-left: 10px;
  color: #fff;
}

.edit-icon {
  color: #fff;
}
.delete-icon:hover {
  transition: all .2s ease-in-out;
  transform: scale(1.1);
  color: #f34141;
}

.edit-icon:hover {
  transition: all .2s ease-in-out;
  transform: scale(1.1);
  color: var(--editcolour);
}
.complete {
  text-decoration: line-through;
  font-family: 'Ubuntu';
  text-decoration-color: #161a2b;
  text-decoration-thickness: 3px;
  
  border-color: rgb(64, 255, 58);
  opacity: 0.4;
}
.complete:hover {
  background-color: rgb(64, 255, 58);
}
@keyframes rainbow-anim {
  100%,0%{
    border-color: rgb(255,0,0);
  }
  8%{
    border-color: rgb(255,127,0);
  }
  16%{
    border-color: rgb(255,255,0);
  }
  25%{
    border-color: rgb(127,255,0);
  }
  33%{
    border-color: rgb(0,255,0);
  }
  41%{
    border-color: rgb(0,255,127);
  }
  50%{
    border-color: rgb(0,255,255);
  }
  58%{
    border-color: rgb(0,127,255);
  }
  66%{
    border-color: rgb(0,0,255);
  }
  75%{
    border-color: rgb(127,0,255);
  }
  83%{
    border-color: rgb(255,0,255);
  }
  91%{
    border-color: rgb(255,0,127);
  }
}